<template>
  <div class="setManager">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" width="500px">
      <div class="username">
        <el-form-item label="用户名称" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
      </div>
      <div class="password">
        <el-form-item label="密码" prop="password">
          <el-input v-model="ruleForm.password" type="password"></el-input>
        </el-form-item>
      </div>
      <div class="confirm-password">
        <el-form-item label="密码确认" prop="password">
          <el-input v-model="ruleForm.password2" type="password"></el-input>
        </el-form-item>
      </div>
      <div class="role">
        <el-form-item label="所属角色" prop="role" width="350px">
          <el-select v-model="ruleForm.role" placeholder="管理员">
            <el-option label="管理员" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </div>
      <div class="name">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="ruleForm.name" width="250px"></el-input>
        </el-form-item>
      </div>
      <div class="department">
        <el-form-item label="所属部门" prop="department">
          <el-input v-model="ruleForm.department" width="250px"></el-input>
        </el-form-item>
      </div>
      <div class="phone">
        <el-form-item label="手机" prop="phone">
        <el-input v-model="ruleForm.phone" width="250px"></el-input>
      </el-form-item>
      </div>
      <div class="email">
        <el-form-item label="邮箱" prop="email">
        <el-input v-model="ruleForm.email" width="250px"></el-input>
      </el-form-item>
      </div>
      <div class="remarks">
        <el-form-item label="备注" prop="remarks">
          <el-input type="textarea" v-model="ruleForm.remarks"></el-input>
        </el-form-item>
      </div>
      <el-form-item align="center">
        <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
  </el-form>
  </div>
</template>

<script>
export default {
  name: 'SetManage',
  data() {
    return {
      ruleForm: {
        username: '',
        password: '',
        password2: '',
        role: '',
        name: '',
        department: '',
        phone: '',
        email: '',
        remarks: ''
      },
      // 校验规则
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入正确密码格式', trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        password2: [
          { required: true, message: '请输入正确密码格式', trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        role: [
          { required: true, message: '请选择所属角色', trigger: 'change' }
        ],
        remarks: [
          { required: true, message: '请填写活动形式', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="scss" scoped>
.setManager{
  margin-top: 50px;
  .el-form{
    width: 500px;
    /deep/  .el-select{
      width: 400px;
    }
  }
}
</style>
